<style>
    .block-text {
        width: 100%;
        text-overflow: ellipsis;
        max-width: 340px;
    }
    .cover {
        width: 220px;

    }
    .cover img {
        width: 100%;
    }

    .m-cover {
        width: 160px;
        margin: auto;
        padding: 0.5em;
    }
    .m-cover img {
        width: 100%;
    }

    .project-info {
        font-size: 1.2em;
    }

    .project-desc {
        text-overflow: hidden;
        border-radius: 0.4em;
    }
</style>

<script>
    import { base_url, base_url_fs, base_url_api } from '../../configure.js';
    import { onMount } from 'svelte';
    import { SkeletonText, SkeletonImage } from 'skeleton-elements/svelte';
    import { showDatePattern, convertProjectStatusFromDBCode, getProjectSpecByCode, numberWithCommas } from '../../components/uiUtility.svelte';
    export let projectInfo = {
        np_project_id: '',
        np_name: 'Loading...',
        np_info: 'Loading...',
        author_name: 'Loading...',
        artist_name: 'Loading...',
        np_type: '',
        np_web: 'Loading...',
        np_licensed_by: 'Loading...',
        np_created_date: 'Loading...',
        np_updated_date: 'Loading...',
        np_status: 'Loading...',
        np_view: 'Loading...'
    };
    export let projectCategoryUsed = [];

    let projectSpec = { 
		fullNameCap: '', 
		fullName: '', 
		dir: '' 
	};

    //Configure
	// let url_fs = 'http://51.68.152.128/';

    onMount(() => {
		projectSpec = getProjectSpecByCode(projectInfo.np_type);
        // console.log("projectSpec", projectSpec);

    });

</script>



<!-- SHOW MOBILE -->
<div class="d-block d-xs-block d-sm-block d-md-block d-lg-none d-xl-none bg-darker rounded pl-4 pr-4 pt-2">
    <div class="bg-inner-content pl-4 pr-4 pt-3 pb-2 mt-2 rounded">
        {#if projectInfo.np_project_id === ''}
        <h3 class="text-white"><SkeletonText effect='wave' tag="h3">.................................................</SkeletonText></h3>
        {:else}
        <h3 class="text-white">{projectInfo.np_name}</h3>
        {/if}
    </div>
    <div class="m-cover">
        {#if projectInfo.np_project_id === ''}
        <SkeletonImage effect='wave' width='220' height='313' />
        {:else}
        <img alt="Project cover" src="{base_url_fs}{getProjectSpecByCode(projectInfo.np_type).dir}/{projectInfo.np_project_id}/{projectInfo.np_project_id}_cover.jpg">
        {/if}
    </div>
    <div class="pt-2 project-desc">
        <div class="bg-inner-content pl-4 pr-4 pt-3 pb-2 mt-2 rounded">
            <i class="fad fa-quote-left mr-2"></i>
            {#if projectInfo.np_project_id === ''}
            <h3 class="text-white"><SkeletonText effect='wave' tag="h3">.................................................</SkeletonText></h3>
            {:else}
            {projectInfo.np_info}
            {/if}
            <i class="fad fa-quote-right ml-2"></i>
        </div>
    </div>
</div>

<!-- SHOW DESKTOP -->
<div class="card card-custom gutter-b bg-darker">
    <div class="card-body">
        {#if projectInfo.np_project_id === ''}
            <div class="float-left pr-4 d-none d-lg-block">
                <div class="cover">
                    <SkeletonImage effect='wave' width='220' height='313' />
                </div>
            </div>
        {:else}
            <div class="float-left pr-4 d-none d-lg-block">
                <div class="cover">
                    <img alt="Project cover" src="{base_url_fs}{getProjectSpecByCode(projectInfo.np_type).dir}/{projectInfo.np_project_id}/{projectInfo.np_project_id}_cover.jpg">
                </div>
            </div>
        {/if}
            <div class="pl-4">
                <div class="d-none d-lg-block d-xl-block">
                    <div class="row">
                        {#if projectInfo.np_project_id === ''}
                        <div class="col-12 pb-2">
                            <h3 class="text-white"><SkeletonText effect='wave' tag="h3">.................................................</SkeletonText></h3>
                        </div>
                        <div class="col-6 text-muted">
                            <table width="100%" class="project-info">
                                <tr>
                                    <td width="40%"><span class="text-white">Author</span></td>
                                    <td width="60%"><SkeletonText effect='wave'>...........................</SkeletonText></td>
                                </tr>
                                <tr>
                                    <td><span class="text-white">Artist</span></td>
                                    <td><SkeletonText effect='wave'>...........................</SkeletonText></td>
                                </tr>
                                <tr>
                                    <td><span class="text-white">Website</span></td>
                                    <td><SkeletonText effect='wave'>...........................</SkeletonText></td>
                                </tr>
                            </table>
                            <br/>

                            <h3>Category</h3>
                            {#each projectCategoryUsed as item}
                            <span class="label label-xl font-weight-boldest label-inline label-primary mr-2">{item.npc_name}</span>
                            {/each}
                        </div>
                        {:else}
                        <div class="col-12 pb-2">
                            <h3 class="text-white">{projectInfo.np_name}</h3>
                        </div>
                        <div class="col-6 text-muted">
                            <table width="100%" class="project-info">
                                <tr>
                                    <td width="40%"><span class="text-white">Author</span></td>
                                    <td width="60%">{projectInfo.author_name}</td>
                                </tr>
                                <tr>
                                    <td><span class="text-white">Artist</span></td>
                                    <td>{projectInfo.artist_name}</td>
                                </tr>
                                <tr>
                                    <td><span class="text-white">Website</span></td>
                                    <td><div class="block-text">{projectInfo.np_web}</div></td>
                                </tr>
                            </table>
                            <br/>
                            <h3>Category</h3>
                            {#each projectCategoryUsed as item}
                            <span class="label label-xl font-weight-boldest label-inline label-primary mr-2">{item.npc_name}</span>
                            {/each}
                        </div>
                        {/if}
                        <div class="col-6">
                            <div class="pr-4 pl-4 pt-2 project-desc bg-inner-content">
                                <div class="p-4">
                                    <i class="fad fa-quote-left mr-2"></i>
                                    {projectInfo.np_info}
                                    <i class="fad fa-quote-right ml-2"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <div class="d-block d-xs-block d-sm-block d-md-block d-lg-none d-xl-none">
                        <div class="row p-0">
                            <div class="col-6 p-0 mb-2">
                                <span class="mr-4 float-left">
                                        <i class="fad fa-sunrise icon-2x text-mute font-weight-bold"></i>
                                    </span>
                                    <div class="d-flex flex-column text-white-75">
                                        <span class="font-weight-bolder font-size-sm">First Release</span>
                                        <span class="font-weight-bolder font-size-h5">
                                        <span class="text-dark-50 font-weight-bold"></span>{showDatePattern(projectInfo.np_created_date)}</span>
                                    </div>
                            </div>
                            <div class="col-6 p-0 mb-2">
                                <span class="mr-4 float-left">
                                        <i class="fad fa-stars icon-2x text-muted font-weight-bold"></i>
                                    </span>
                                    <div class="d-flex flex-column text-white-75">
                                        <span class="font-weight-bolder font-size-sm">Lastest Update</span>
                                        <span class="font-weight-bolder font-size-h5">
                                            {showDatePattern(projectInfo.np_updated_date)}
                                        </span>
                                    </div>
                            </div>
                            <div class="col-6 p-0 mb-2">
                                <span class="mr-4 float-left">
                                    <i class="fad fa-flag icon-2x text-muted font-weight-bold"></i>
                                </span>
                                <div class="d-flex flex-column flex-lg-fill">
                                    <span class="text-white-75 font-weight-bolder font-size-sm">Status</span>
                                    <span class="font-weight-bolder font-size-h5">{convertProjectStatusFromDBCode(projectInfo.np_status)}</span>
                                </div>
                            </div>
                            <div class="col-6 p-0 mb-2">
                                <span class="mr-4 float-left">
                                    <i class="fad fa-eye icon-2x text-muted font-weight-bold"></i>
                                </span>
                                <div class="d-flex flex-column">
                                    <span class="text-white-75 font-weight-bolder font-size-sm">Views</span>
                                    <span class="font-weight-bolder font-size-h5">
                                    {numberWithCommas(projectInfo.np_view)}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- INFO BAR : DESKTOP -->
                    <div class="d-none d-lg-block d-xl-block">
                        <div class="row">
                            <div class="col-12">
                                <div class="separator separator-solid my-7"></div>
                                <div class="d-flex align-items-center flex-wrap">
                                    <div class="d-flex align-items-center flex-lg-fill mr-5 my-1">
                                        <span class="mr-4">
                                            <i class="fad fa-sunrise icon-2x text-mute font-weight-bold"></i>
                                        </span>
                                        <div class="d-flex flex-column text-white-75">
                                            <span class="text-dark-50 font-weight-bolder font-size-sm">First Release</span>
                                            <!-- <span class="text-dark-50 font-weight-bold">dd-mm-yyyy</span> -->
                                            <span class="font-weight-bolder font-size-h5">
                                                {#if projectInfo.np_created_date !== 'Loading...'}
                                                {showDatePattern(projectInfo.np_created_date)}
                                                {:else}
                                                {projectInfo.np_created_date}
                                                {/if}
                                            </span>
                                        </div>
                                    </div>
                                    <div class="d-flex align-items-center flex-lg-fill mr-5 my-1">
                                        <span class="mr-4">
                                            <i class="fad fa-stars icon-2x text-muted font-weight-bold"></i>
                                        </span>
                                        <div class="d-flex flex-column text-white-75">
                                            <span class="text-dark-50 font-weight-bolder font-size-sm">Lastest Update</span>
                                            <span class="font-weight-bolder font-size-h5">
                                                {#if projectInfo.np_updated_date !== 'Loading...'}
                                                {showDatePattern(projectInfo.np_updated_date)}
                                                {:else}
                                                {projectInfo.np_updated_date}
                                                {/if}
                                            </span>
                                        </div>
                                    </div>
                                    <div class="d-flex align-items-center flex-lg-fill mr-5 my-1">
                                        <span class="mr-4">
                                            <i class="fad fa-flag icon-2x text-muted font-weight-bold"></i>
                                        </span>
                                        <div class="d-flex flex-column flex-lg-fill">
                                            <span class="text-dark-50  font-weight-bolder font-size-sm">Status</span>
                                            <span class="font-weight-bolder font-size-h5">
                                            {convertProjectStatusFromDBCode(projectInfo.np_status)}
                                            </span>
                                        </div>
                                    </div>
                                    <div class="d-flex align-items-center flex-lg-fill mr-5 my-1">
                                        <span class="mr-4">
                                            <i class="fad fa-eye icon-2x text-muted font-weight-bold"></i>
                                        </span>
                                        <div class="d-flex flex-column">
                                            <span class="text-dark-50  font-weight-bolder font-size-sm">Views</span>
                                            <span class="font-weight-bolder font-size-h5">
                                            {numberWithCommas(projectInfo.np_view)}
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
</div>