<style>
    .cursor-pointer {
        cursor: pointer;
    }
    .btnPaypal {
        width: 160px;
    }
    .paymentWindow {
        color: #333;
    }
    .avatarCat {
        width: 100%;
        max-width: 80px;
    }
    .closeBtn {
        padding: 1em 2em 0 0;
        text-align: right;
    }
</style>

<script>
    import Modal from '../Modal.svelte';

    export let mode = "big";

    let windowWidth;
    let modalOpen = false;

    let inputBaht = 500;
    $:inputFee = Math.ceil((inputBaht*0.04) + 11);
    $:inputFeePercent = Math.ceil((inputFee/inputBaht)*100);

    function target_popup(form) {
        window.open('', 'formpopup', 'width=400,height=400,resizeable,scrollbars');
        form.target = 'formpopup';
    }
</script>

<svelte:window 
    bind:innerWidth={windowWidth}
/>

<Modal bind:open={modalOpen}>
    <div class="closeBtn" on:click={()=>modalOpen = false}><i class="fal fa-times icon-3x"></i></div>
    <div class="paymentWindow pl-10 pr-10 pb-4">
        <table>
            <tr>
                <td align="center"><img src="demo/bakeneko-manga.png" class="avatarCat" alt=""/></td>
                <td align="center"><img src="demo/bakeneko-doujin.png" class="avatarCat" alt=""/></td>
                <td align="center"><img src="demo/bakeneko-novel.png" class="avatarCat" alt=""/></td>
                <td align="center"><img src="demo/bakeneko-onovel.png" class="avatarCat" alt=""/></td>
            </tr>
            <tr>
                <td colspan="4">
                    <div class="p-4 bg-secondary rounded mt-4">
                        <h2>Please Support Us</h2>
                        Nekopost มีค่าบริการด้านระบบไอทีเป็นรายเดือน และเพื่อให้ทีมแอดมินสามารถบริการเวปไซต์ได้
                        ทุกท่านสามารถร่วมสนับสนุนเราผ่านช่องทางด้านล่างครับ
                        <b>ไม่มีค่าธรรมเนียมสำหรับผู้สนับสนุนครับ แต่ Paypal จะหักจากคนรับโดยคิดเป็น %
                        ยกตัวอย่างเช่น</b>
                    </div>
                </td>
            </tr>
            <tr>
                <td colspan="4">
                    <table width="100%" class="table thead-light">
                        <thead>
                            <tr>
                                <td>จำนวนตัวอย่าง</td>
                                <td>ค่าธรรมเนียม</td>
                                <td>ที่ได้รับ</td>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>50 บาท</td>
                                <td>12.95 (25.9%)</td>
                                <td>37.05</td>
                            </tr>
                            <tr>
                                <td>100 บาท</td>
                                <td>14.9 (14.9%)</td>
                                <td>85.1</td>
                            </tr>
                            <tr>
                                <td>300 บาท</td>
                                <td>23 (7.7%)</td>
                                <td>277</td>
                            </tr>
                            <tr>
                                <td><input type="number" bind:value={inputBaht} style="width: 80px; border: 1px solid #666;"> บาท (คำนวน)</td>
                                <td>{inputFee} ({inputFeePercent}%)</td>
                                <td>{inputBaht - inputFee > 0 ? inputBaht - inputFee : 0}</td>
                            </tr>
                        </tbody>
                    </table>
                </td>
            </tr>
        </table>
        <table>
            <tr>
                <td colspan="2">
                    <form action="https://www.paypal.com/cgi-bin/webscr" method="post" target="_blank">
                        <input type="hidden" name="cmd" value="_s-xclick">
                        <input type="hidden" name="hosted_button_id" value="RK3Y6N4GXBMUS">
                        <input type="image" src="demo/paypal.png" class="btnPaypal rounded" border="0" name="submit" alt="PayPal – The safer, easier way to pay online!">
                        <img alt="" border="0" src="https://www.paypalobjects.com/en_GB/i/scr/pixel.gif" width="1" height="1">
                    </form>
                </td>
                <td colspan="2" align="center" valign="top" class="p-2">
                    <b>Note</b> เมื่อกดปุ่ม Paypal จะมีช่องให้กรอก description สำหรับฝากข้อความถึงแอด และ price per item คือยอดที่ต้องการสนับสนุนเวปน้องแมวครับ
                </td>
            </tr>
        </table>
    </div>
</Modal>

{#if mode === "big"}
<div class="card card-custom bgi-no-repeat gutter-b bg-white-o-90 cursor-pointer" style="background-size: cover; background-image: url(/assets/media/custom/bg-red.jpg)" on:click={()=>modalOpen = true}>
    <div class="card-body">
        <a href class="text-white font-weight-bolder line-height-lg font-size-h4">
            Support Us <i class="fa fa-heart"></i>
        </a>
    </div>
</div>
{/if}