<script>
    export let item = '';
    export let lazy = false;
    export let styleX = '';
    export let classX = '';

    let src = lazy ? "" : item;
    let observer = null;

    $:if(lazy == false) {
        src = item;
    } else {
        observer = new IntersectionObserver(onIntersect, {rootMargin: '200px'});
    }

    function onIntersect(entries) {
        if (!src && entries[0].isIntersecting) {
            src = item;
            //console.log("Transfer!");
        }
    }

    function lazyLoad(node) {
        //console.log("Lazyload triggered!");
        observer && observer.observe(node);

        return {
            destroy() {
                observer && observer.unobserve(node)
            }
        }
    }
</script>

<style>
    img {
        box-shadow: 0.1em;
        height: 100%;
        max-width: 1200px;
    }

    .img-cover {
        width: 100%;
		border-radius: 0.4em;
		display: block;
		margin: auto;
		position: absolute;
        transition: transform .2s;
    }

    .img-preview {
        width: 72px!important;
		height: 72px!important;
		border-radius: 0.4em;
		float: left;
		margin-right: 1em;
    }
</style>

<article use:lazyLoad>
<img src={src}
    class='{classX}' 
    style='{styleX}' 
    alt='' 
    onerror="this.src='./demo/crissXcross.png';"
/>
</article>